//JavaScript Document
import React, { Component, Fragment } from "react";
import { Button } from 'antd-mobile';
export default class User extends Component {
    constructor() {
        super();
    }
    render() {
        return <Fragment>
            <div className="container">
                <div className="float-right" onClick={this.handleDetail.bind(this)}>活动详情</div>
                <div className="align-center section">
                    <div className="title">签约本活动，预计全年可节省</div>
                    <div className="main">120<span className="sub">度</span></div>
                    <div className="title">预计可获得电费返利</div>
                    <div  className="main">100<span className="sub">元</span></div>                 
                </div> 
                <Button type="primary"  style={{ width: '180px',height:'50px',lineHeight:"50px",margin:"0 auto 100px",fontSize:"16px"}}
                   onClick={this.handleSign.bind(this)}>我要签约</Button>
                <div className="footer">*活动解释权归XX所有，测算数据仅供参考，以实际参与结果为准</div>
            </div>
        </Fragment>;
    }
    handleSign(){
        this.props.history.push("./sign")
    }
    handleDetail(){
        this.props.history.push("./detail")
    }
};